import { omit, map as lodashMap, isEqual } from 'lodash-es';

// 头像及isInitialized不参与对比
const isEqualValue = (value: Record<string, any>, otherValue: Record<string, any>): boolean => {
  const v1 = omit({
    ...value,
    comment: value.comment || '',
    place: value.place || '',
    topReceivers: lodashMap(value.topReceivers, (o) => omit(o, 'avatarMediaId')),
  }, ['isInitialized', 'mode']);
  const v2 = omit({
    ...otherValue,
    comment: otherValue.comment || '',
    place: otherValue.place || '',
    topReceivers: lodashMap(otherValue.topReceivers, (o) => omit(o, 'avatarMediaId')),
  }, ['isInitialized', 'mode']);

  return isEqual(v1, v2);
};

export default isEqualValue;
